import { useState, useCallback } from "react";
import { PortalPopup } from "./PortalPopup";
import PopoverPopupState from "./PopoverPopupState";
import { DeptIc } from "./DeptIc";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

function ClubCard(props) {
    const { name="" } = props;
    const { campus="" } = props;
    const { content="" } = props;
    const { image="" } = props;
    const { facebook="" } = props;
    const { instagram="" } = props;
    const { other="" } = props;
    const links = {
        facebook: facebook,
        instagram: instagram,
        other: other
    }
    
    const [isDeptIcOpen, setDeptIcOpen] = useState(false);
    
    const openDeptIc = useCallback(() => {
        setDeptIcOpen(true);
    }, []);
    
    const closeDeptIc = useCallback(() => {
        setDeptIcOpen(false);
    }, []);

    return (
        <Box sx={{
            mx: "6px"
        }}>
            <div className="div84">
                <div className="info-div">

                    <Stack>
                        <div className="club-name-div">
                            <Box sx={{
                                position: 'absolute', 
                                top: '15px',
                                left: '60px',
                                backgroundColor: '#d9d9d9', 
                                width: '75px',
                                height: '10px',
                                zIndex: 0,
                            }} />
                            {/* <div className="div86" /> */}
                            <Typography sx={{...{
                                position: 'relative', 
                                width: '100%',
                                height: '100%',
                                fontWeight: 'bold',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                zIndex: 1,
                            },
                            ...{fontSize: name.length > 8 && '12px'}, 
                            ...{letterSpacing: name.length > 10 && '-1px'}
                            }}>{name}</Typography>
                        </div>
                        <Box sx={{
                            backgroundColor: '#d9d9d9',
                            width: '135px',
                            height: '87px',
                        }}>
                            <img width="100%" src={image} title={name} alt="" />
                        </Box>
                        <Box sx={{
                            my: '5px',
                            display: 'flex',
                            justifyContent: 'center',
                        }}>
                            <PopoverPopupState 
                                title={name} 
                                content={content}
                                image={image}
                                links={links}
                            />
                        </Box>
                    </Stack>

                </div>
                <Box sx={{
                    position: 'absolute',
                    left: '0.4rem',
                    top: '-0.2rem',
                    width: '4rem',
                    height: '0.4rem',
                    backgroundColor: '#e0c5af',
                    borderBottom: '1px solid #000',
                    borderRight: '1px solid #000',
                    borderLeft: '1px solid #000',
                }}>
                    <Typography sx={{
                        fontWeight: 'bold',
                        fontSize: '12px',
                        lineHeight: '0'
                    }}>{campus}</Typography>
                </Box>
                {/* {isDeptIcOpen && (
                <PortalPopup
                    overlayColor="rgba(113, 113, 113, 0.3)"
                    placement="Centered"
                    onOutsideClick={closeDeptIc}
                >
                    <DeptIc 
                        onClose={closeDeptIc} 
                        title={name} 
                        content={content}
                        image={image}
                        links={links}
                    />
                </PortalPopup>
                )} */}
            </div>
        </Box>
    )
}
export default ClubCard;